<template>
    <div>
        <header id="head_top">
            <section class="head_goback" @click="goBack">
                <i class="el-icon-arrow-left"></i>
            </section>
            <section class="title_head ellipsis">
                <span class="title_text">
                    重置密码
                </span>
            </section>
        </header>
        <div class="restForm">
            <section class="phone_number input_container">
                <input type="text" placeholder="账号" v-model="Id">
            </section>
            <section class="input_container">
                <input type="text" placeholder="旧密码" v-model="oldPwd">
            </section>
            <section class="input_container">
                <input type="text" placeholder="请输入新密码" v-model="newPwd">
            </section>
            <section class="input_container">
                <input type="text" placeholder="请确认密码" v-model="againPwd">
            </section>
            <section class="input_container captcha_code_container">
                <input type="text" placeholder="验证码" v-model="captcha_code">
                <div class="img_change_img">
                    <img :src="capUrl" alt="">
                    <div class="change_img" @click="changeCap">
                        <p>看不清</p>
                        <p>换一张</p>
                    </div>
                </div>
            </section>
        </div>
        <div class="login_container" @click="changePwd">确认修改</div>
        <div class="alet_container" v-if="isShow">
            <section class="tip_text_container" >
                <div class="tip_icon">
                    <span></span>
                    <span></span>
                </div>
                <p class="tip_text">修改密码成功</p>
                <div class="confrim" @click="confrim">确认</div>
            </section>
        </div>
        <div class="alet_container" v-if="notCodeisShow">
            <section class="tip_text_container">
                <div class="tip_icon">
                    <span></span>
                    <span></span>
                </div>
                <p class="tip_text">请输入验证码</p>
                <div class="confrim" @click="confrim">确认</div>
            </section>
        </div>
        <div class="alet_container" v-if="CodErrisShow">
            <section class="tip_text_container">
                <div class="tip_icon">
                    <span></span>
                    <span></span>
                </div>
                <p class="tip_text">验证码不正确</p>
                <div class="confrim" @click="confrim">确认</div>
            </section>
        </div>
        <div class="alet_container" v-if="notOldPwd">
            <section class="tip_text_container">
                <div class="tip_icon">
                    <span></span>
                    <span></span>
                </div>
                <p class="tip_text">请输入旧密码</p>
                <div class="confrim" @click="confrim">确认</div>
            </section>
        </div>
        <div class="alet_container" v-if="notNewPwd">
            <section class="tip_text_container">
                <div class="tip_icon">
                    <span></span>
                    <span></span>
                </div>
                <p class="tip_text">请输入旧密码</p>
                <div class="confrim" @click="confrim">确认</div>
            </section>
        </div>
        <div class="alet_container" v-if="notNewPwd">
            <section class="tip_text_container">
                <div class="tip_icon">
                    <span></span>
                    <span></span>
                </div>
                <p class="tip_text">请输入确认密码</p>
                <div class="confrim" @click="confrim">确认</div>
            </section>
        </div>
        <div class="alet_container" v-if="notId">
            <section class="tip_text_container">
                <div class="tip_icon">
                    <span></span>
                    <span></span>
                </div>
                <p class="tip_text">请输入正确的账号</p>
                <div class="confrim" @click="confrim">确认</div>
            </section>
        </div>
    </div>

</template>

<script>
    export default {
        name: "Forget",
        data(){
          return {
              Id:"",
              oldPwd:"",
              newPwd:"",
              againPwd:"",
              captcha_code:"",
              //验证码图片
              capUrl:"",
              isShow:false,
              notCodeisShow:false,
              CodErrisShow:false,
              notOldPwd:false,
              notNewPwd:false,
              notagainPwd:false,
              notId:false,
          }
        },
        created(){
            this.requestCode();
        },
        methods:{
            goBack(){
                this.$router.push({
                    path:'/info'
                })
            },
            //请求验证码
            requestCode(){
                this.axios.post("v1/captchas").then(res=>{
                    // console.log(res);
                    //获取到的base64可直接赋给图片的URL
                    this.capUrl = res.code;
                }).catch(err=>{})
            },
            //点击换验证码
            changeCap(){
                this.requestCode();
            },
            //修改密码
            changePwd(){
                this.axios.post("v2/changepassword",{
                    username:this.Id,
                    oldpassWord:this.oldPwd,
                    newpassword:this.newPwd,
                    confirmpassword:this.againPwd,
                    captcha_code:this.captcha_code
                }).then(res=>{
                    console.log(res);
                    this.passwordFun(res)

                }).catch(err=>{})
            },
            confrim(){
                this.isShow=false;
                this.notOldPwd=false
                this.notNewPwd=false
                this.notagainPwd=false
                this.notCodeisShow=false
                this.notId=false
                this.CodErrisShow=false
            },
            passwordFun(res){
                if (res.success == '密码修改成功'){
                    this.isShow=true
                }
                if (res.message == '必修添加旧密码'){
                    this.notOldPwd=true
                }
                if (res.message == '必修填写新密码'){
                    this.notNewPwd=true
                }
                if (res.message == '必修填写确认密码'){
                    this.notagainPwd=true
                }
                if (res.message == '请填写验证码'){
                    this.notCodeisShow=true
                }
                if (res.message == '用户名参数错误'){
                    this.notId=true
                }
                if (res.message == '验证码不正确'){
                    this.CodErrisShow=true
                }

            }
        },
    }
</script>

<style scoped>
    #head_top{
        background-color: #3190e8;
        position: fixed;
        z-index: 100;
        left: 0;
        top: 0;
        width: 100%;
        height: 1.95rem;
    }
    .head_goback{
        left: .4rem;
        width: .6rem;
        height: 1rem;
        line-height: 2.2rem;
        margin-left: .4rem;
        color: #fff;
    }
    .title_head{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: 50%;
        color: #fff;
        text-align: center;
    }
    .title_text {
        font-size: .8rem;
        color: #fff;
        text-align: center;
        font-weight: 700;
    }
    .restForm{
        background-color: #fff;
        margin-top: 2.5rem;
    }
    .restForm .phone_number{
        padding: .3rem .8rem;
    }
    .restForm .input_container{
        display: flex;
        justify-content: space-between;
        padding: .6rem .8rem;
        border-bottom: 1px solid #f1f1f1;
    }
    .restForm .captcha_code_container{
        height: 2.2rem;
    }
    .input_container input{
        font-size: .7rem;
        color: #666;
        border: none;
        outline: none;
    }
    .img_change_img {
        display: flex;
        align-items: center;
    }
    .change_img{
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        width: 2rem;
        justify-content: center;
    }
    .change_img p{
        font-size: .55rem;
        color: #666;
    }
    .change_img p:nth-of-type(2) {
        color: #3b95e9;
        margin-top: .2rem;
    }
    .login_container {
        margin: 1rem .5rem;
        font-size: .7rem;
        color: #fff;
        background-color: #4cd964;
        padding: .5rem 0;
        border: 1px;
        border-radius: .15rem;
        text-align: center;
    }
    .alet_container {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 200;
    }
    .tip_text_container{
        position: absolute;
        top: 50%;
        left: 50%;
        margin-top: -6rem;
        margin-left: -6rem;
        width: 12rem;
        animation: tipMove .4s;
        background-color: #fff;
        padding-top: .6rem;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        border: 1px;
        border-radius: .25rem;
    }
    .tip_text_container .tip_icon{
        width: 3rem;
        height: 3rem;
        border: .15rem solid #f8cb86;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
    .tip_icon span:first-of-type {
        width: .12rem;
        height: 1.5rem;
        background-color: #f8cb86;
    }
    .tip_icon span:nth-of-type(2) {
        width: .2rem;
        height: .2rem;
        border: 1px;
        border-radius: 50%;
        margin-top: .2rem;
        background-color: #f8cb86;
    }
    .tip_text_container .tip_text{
        font-size: .7rem;
        color: #333;
        line-height: .9rem;
        text-align: center;
        margin-top: .8rem;
        padding: 0 .4rem;
    }
    .confrim {
        font-size: .8rem;
        color: #fff;
        font-weight: 700;
        margin-top: .8rem;
        background-color: #4cd964;
        width: 100%;
        text-align: center;
        line-height: 1.8rem;
        border: 1px;
        border-bottom-left-radius: .25rem;
        border-bottom-right-radius: .25rem;
    }
</style>